import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { ZpageHeader, Ztabs } from "zerod";
import { Icon } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZpageHeader/doc.md";
import pageHeader from "@/views/Main/component-doc/ZpageHeader-doc/pageHeader";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			pageHeader = {...pageHeader,trademark:null};
			render() {
				return (
					<Router>
						<ZpageHeader {...this.pageHeader} />
					</Router>
				);
			}
		}
		return <Myjavascript />;
	},
	demo2: function() {
		class Myjavascript extends React.PureComponent {
			pageHeader = pageHeader;
			tabPanes = [
				{ tab: "基本信息", key: "1", content: "" },
				{ tab: "配置信息", key: "2", content: "" },
				{
					tab: "其他信息",
					key: "3",
					content: () => {
						return "";
					},
				},
			];
			render() {
				return (
					<Router>
						<ZpageHeader
							{...this.pageHeader}
							children={() => {
								return <Ztabs tabPanes={this.tabPanes} />;
							}}
						/>
					</Router>
				);
			}
		}
		return <Myjavascript />;
	},
});
